import React, { PropsWithChildren } from 'react'
import styled, { css } from 'styled-components'
import { color, zindex, media } from '../../Settings'
import { FRAME_SPACING } from '../../PageLayout'
import { WHEEL_ITEM_SIZE } from './Wheel'
import { rgba } from 'polished'

const sceneWheelHeightStyles = css`
  height: 21.5rem;

  ${media.minMd} {
    height: 23rem;
  }
`

const SceneWheelWrap = styled.div`
  background-color: ${color.rome};
  padding-left: ${FRAME_SPACING};
  padding-right: ${FRAME_SPACING};
  ${sceneWheelHeightStyles};
`

const SceneWheelInner = styled.div`
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  max-width: 300px;
  margin-left: auto;
  margin-right: auto;

  &:before,
  &:after {
    position: absolute;
    left: 0;
    right: 0;
    content: '';
    display: block;
    height: calc(50% - ${WHEEL_ITEM_SIZE}px / 2);
    z-index: ${zindex.step};
    pointer-events: none;
  }
  &:before {
    top: -0.5px;
    background: linear-gradient(
      to top,
      ${rgba(color.rome, 0.6)} 0%,
      ${rgba(color.rome, 0.99)} 100%
    );
    border-bottom: 0.5px solid ${rgba(color.london, 0.3)};
  }
  &:after {
    bottom: -0.5px;
    background: linear-gradient(
      to bottom,
      ${rgba(color.rome, 0.6)} 0%,
      ${rgba(color.rome, 0.99)} 100%
    );
    border-top: 0.5px solid ${rgba(color.london, 0.3)};
  }
`

export const SceneWheel = (props: PropsWithChildren<{}>) => {
  const { children } = props

  return (
    <SceneWheelWrap>
      <SceneWheelInner>{children}</SceneWheelInner>
    </SceneWheelWrap>
  )
}

export const SceneWheelPlaceholder = styled.div`
  ${sceneWheelHeightStyles};
`
