import React from 'react'
import styled, { css } from 'styled-components'
import { Link, ExternalLink, linkIconStyles } from '../Link'
import { color, media } from '../Settings'
import { scrollToElement } from '../utils'
import Svg from '../Svg'

const Wrap = styled.div`
  display: flex;
  margin-left: -3rem;
  ${media.maxXs} {
    > *:first-child {
      display: none;
    }
  }
`

const linkStyles = css`
  display: flex;
  align-items: center;
  margin-left: 3rem;
`

const LinkSvg = styled.span`
  ${linkIconStyles};
  margin-left: 1rem;
`

const Links = () => {
  return (
    <Wrap>
      <Link scroll={scrollToElement} to="/examples" css={linkStyles}>
        Examples
        <LinkSvg>
          <Svg icon="arrowRightThick" color={color.riga} />
        </LinkSvg>
      </Link>
      <ExternalLink
        href="https://github.com/davidcetinkaya/embla-carousel"
        css={linkStyles}
      >
        GitHub
        <LinkSvg>
          <Svg icon="githubLogo" color={color.riga} />
        </LinkSvg>
      </ExternalLink>
    </Wrap>
  )
}

export default Links
