import React from 'react'
import { useInView } from 'react-intersection-observer'
import { SceneWheel, SceneWheelPlaceholder } from './SceneWheel'
import Wheel from './Wheel'

type PropType = {
  loop?: boolean
}

const Carousel = (props: PropType) => {
  const { loop } = props

  return (
    <SceneWheel>
      <Wheel slideCount={24} perspective="left" loop={loop} label="hours" />
      <Wheel slideCount={60} perspective="right" loop={loop} label="min" />
    </SceneWheel>
  )
}

const CarouselWithPlaceholder = (props: PropType) => {
  const [ref, inView] = useInView({ triggerOnce: true })

  return (
    <div ref={ref}>
      {inView ? <Carousel {...props} /> : <SceneWheelPlaceholder />}
    </div>
  )
}

export default CarouselWithPlaceholder
