import React, { PropsWithChildren } from 'react'
import styled, { css } from 'styled-components'
import { media, color, transition, zindex } from '../Settings'
import { visuallyHiddenStyles } from '../VisuallyHiddenStyles'
import { createSquareSizeStyles } from '../utils'
import { PropType } from '../Button/Button'
import { Button } from '../Button'
import { useNavigation } from '.'

const Wrap = styled(Button)`
  position: fixed;
  background-color: ${color.lisbon};
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  z-index: ${zindex.navigation + zindex.step};
  border-radius: 50%;
  bottom: 2rem;
  right: 2rem;
  ${media.minSm} {
    display: none;
  }

  ${createSquareSizeStyles('5rem')}
`

const Burger = styled.div`
  position: relative;

  > span {
    background-color: ${color.prague};
    display: block;
    height: 2px;
    border-radius: 2px;
    position: relative;
    width: 25px;
    transition: background-color ${transition.default};

    &:before,
    &:after {
      position: absolute;
      content: '';
      background-color: ${color.prague};
      border-radius: 2px;
      display: block;
      height: 100%;
      width: 100%;
      transition: transform ${transition.default};
    }

    &:before {
      transform: translateY(8px);
    }
    &:after {
      transform: translateY(-8px);
    }
  }
`

const activeStyles = css`
  > span {
    background-color: transparent;

    &:before {
      transform: rotate(-45deg) translateY(0);
    }
    &:after {
      transform: rotate(45deg) translateY(0);
    }
  }
`

const HelperText = styled.span`
  ${visuallyHiddenStyles}
`

const NavButton = (props: PropsWithChildren<PropType>) => {
  const { isOpen } = useNavigation()

  return (
    <Wrap {...props}>
      <Burger
        css={css`
          ${isOpen ? activeStyles : ''}
        `}
      >
        <span />
      </Burger>
      <HelperText>Toggle navigation open</HelperText>
    </Wrap>
  )
}

export default NavButton
