import React, { useEffect, useState, useCallback, useRef } from 'react'
import { useEmblaCarousel } from 'embla-carousel/react'
import { EmblaCarouselType } from 'embla-carousel'
import { color } from '../../Settings'
import styled from 'styled-components'

const CIRCLE_DEGREES = 360
export const WHEEL_ITEM_SIZE = 32
const WHEEL_ITEM_COUNT = 18
const WHEEL_ITEMS_IN_VIEW = 4
const WHEEL_ITEM_RADIUS = CIRCLE_DEGREES / WHEEL_ITEM_COUNT
const WHEEL_RADIUS = Math.round(
  WHEEL_ITEM_SIZE / 2 / Math.tan(Math.PI / WHEEL_ITEM_COUNT),
)
const IN_VIEW_DEGREES = WHEEL_ITEM_RADIUS * WHEEL_ITEMS_IN_VIEW

const Label = styled.div`
  font-weight: bold;
  transform: translateX(-5.5rem);
  pointer-events: none;
`

const Container = styled.div`
  height: ${WHEEL_ITEM_SIZE}px;
  width: 100%;
  perspective: 1000px;
`

const Wrap = styled.div`
  height: 100%;
  display: flex;
  align-items: center;
  min-width: 50%;
  line-height: 1;
  font-size: 1.8rem;

  &.perspective-left {
    ${Container} {
      perspective-origin: calc(50% + ${100 + WHEEL_ITEM_SIZE}px) 50%;
      transform: translateX(-10px) !important;
    }

    ${Label} {
      transform: translateX(-78px);
    }
  }

  &.perspective-right {
    ${Container} {
      perspective-origin: calc(50% - ${100 + WHEEL_ITEM_SIZE}px) 50%;
      transform: translateX(-30px) !important;
    }

    ${Label} {
      transform: translateX(-70px);
    }
  }
`

const Carousel = styled.div`
  background-color: ${color.rome};
  min-width: 100%;
  height: 100%;
  overflow: hidden;
`

const Viewport = styled.div`
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -webkit-tap-highlight-color: transparent;
`

const Slide = styled.div`
  position: absolute;
  top: 0 !important;
  left: 0 !important;
  width: 100%;
  height: 100%;
  font-size: 19px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  backface-visibility: hidden;
  opacity: 0;
`

const isInView = (wheelLocation: number, slidePosition: number): boolean =>
  Math.abs(wheelLocation - slidePosition) < IN_VIEW_DEGREES

const slideStyle = (
  embla: EmblaCarouselType,
  index: number,
  loop: boolean,
  slideCount: number,
  totalRadius: number,
  wheelRotation: number,
) => {
  const wheelLocation = embla.scrollProgress() * totalRadius
  const positionDefault = embla.scrollSnapList()[index] * totalRadius
  const positionLoopEnd = positionDefault - totalRadius
  const positionLoopStart = positionDefault + totalRadius

  let inView = false
  let angle = index * -WHEEL_ITEM_RADIUS

  if (isInView(wheelLocation, positionDefault)) {
    inView = true
  }

  if (loop && isInView(wheelLocation, positionLoopEnd)) {
    inView = true
    angle = -CIRCLE_DEGREES + (slideCount - index) * WHEEL_ITEM_RADIUS
  }

  if (loop && isInView(wheelLocation, positionLoopStart)) {
    inView = true
    angle = -(totalRadius % CIRCLE_DEGREES) - index * WHEEL_ITEM_RADIUS
  }

  if (inView) {
    return {
      opacity: 1,
      transform: `rotateX(${
        angle + wheelRotation
      }deg) translateZ(${WHEEL_RADIUS}px)`,
    }
  }

  return { opacity: 0, transform: 'none' }
}

const getSlideStyles = (
  embla: EmblaCarouselType | undefined,
  loop: boolean,
  slideCount: number,
  totalRadius: number,
  wheelRotation: number,
) => {
  const styles = []
  for (let index = 0; index < slideCount; index += 1) {
    const style = embla
      ? slideStyle(embla, index, loop, slideCount, totalRadius, wheelRotation)
      : {}
    styles.push(style)
  }
  return styles
}

type PropType = {
  loop?: boolean
  label: string
  slideCount: number
  perspective: 'left' | 'right'
}

const Wheel = (props: PropType) => {
  const { slideCount, perspective, label, loop = false } = props
  const [emblaRef, embla] = useEmblaCarousel({
    loop,
    axis: 'y',
    dragFree: true,
    draggableClass: '',
    draggingClass: '',
    selectedClass: '',
  })
  const [wheelReady, setWheelReady] = useState(false)
  const [wheelRotation, setWheelRotation] = useState(0)
  const rootElement = useRef<HTMLDivElement>(null)
  const rootElementSize = useRef(0)
  const totalRadius = slideCount * WHEEL_ITEM_RADIUS
  const rotationOffset = loop ? 0 : WHEEL_ITEM_RADIUS
  const slideStyles = getSlideStyles(
    embla,
    loop,
    slideCount,
    totalRadius,
    wheelRotation,
  )

  const getRootElementSize = useCallback(() => {
    if (!rootElement.current) return 0
    return rootElement.current.getBoundingClientRect().height
  }, [])

  const setRotation = useCallback(() => {
    if (!embla) return
    const rotation = slideCount * WHEEL_ITEM_RADIUS - rotationOffset
    setWheelRotation(rotation * embla.scrollProgress())
  }, [setWheelRotation, slideCount, rotationOffset, embla])

  useEffect(() => {
    if (embla) {
      rootElementSize.current = getRootElementSize()
      setWheelReady(true)
      embla.dangerouslyGetEngine().translate.toggleActive(false)
      setRotation()

      embla.on('pointerUp', () => {
        const { scrollTo, target, location } = embla.dangerouslyGetEngine()
        const distanceToTarget = target.get() - location.get()
        scrollTo.distance(distanceToTarget * 0.1, true)
      })

      embla.on('scroll', () => {
        setRotation()
      })

      embla.on('resize', () => {
        const newRootElementSize = getRootElementSize()
        if (rootElementSize.current === newRootElementSize) return

        rootElementSize.current = newRootElementSize
        setWheelReady(false)
        setWheelReady(() => {
          embla.reInit()
          embla.dangerouslyGetEngine().translate.toggleActive(false)
          setRotation()
          return true
        })
      })
    }
  }, [embla, setRotation, getRootElementSize])

  return (
    <Wrap className={`perspective-${perspective}`}>
      <Carousel ref={rootElement}>
        <Viewport ref={emblaRef}>
          <Container>
            {slideStyles.map((slideStyle, index) => (
              <Slide
                key={index}
                style={wheelReady ? slideStyle : { transform: 'none' }}
              >
                {index}
              </Slide>
            ))}
          </Container>
        </Viewport>
      </Carousel>
      <Label>{label}</Label>
    </Wrap>
  )
}

export default Wheel
